body{
  background: rgb(63,94,251);
  background: radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(100,162,66,0.9528186274509804) 91%, rgba(252,70,70,1) 100%);
  text-align: center;
  color: #282c34;;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  }

  .data img{
    background-color: #18232B;
    border-radius: 50%;
    padding: 10px;
    margin-top: -60px;
  }

.form{
  background: rgb(116,57,199);
  background: linear-gradient(0deg, rgba(116,57,199,0.6194852941176471) 17%, rgba(45,253,146,1) 100%);
}

.weather-description h3{
  color: rgb(21, 28, 28);
  font-weight:400;
}
.weather-description p{
  color: rgb(21, 28, 28);
  font-size: 40px;
  margin: 0;
}

.title{
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 0;
}

.location{
font-weight: 400;
}

.header-div{
  background-color: hwb(132 77% 4%);
  margin: 20px;
  padding: 100px 0 0 0;
}

.header-div div{
  background: white;
  text-align: center;
  padding-bottom: 10px;
}

input{
border-radius: 20px;
outline: none;
border: transparent 1px;
padding: 10px;
margin: 10px 5px;
font-weight: 500;
font-family: Verdana, Geneva, Tahoma, sans-serif;
}

button{
  border-radius: 20px;
  outline: none;
  border: transparent 1px;
  color: white;
  background-color: #282c34;
  padding: 10px;
  margin: 10px 5px;
  font-weight: 500;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.App {
  text-align: center;
}


@media screen and (min-width:768px) {
  .weather-description{
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 30px;
    margin-top: 30px;
    text-align: center;
  }

  .header-div{
    margin: 20px 90px 20px 90px;
  }
  section{
    text-align: center;
  }

}

